













































	//高德SDK
	import amap from '@/common/SDK/amap-wx.js';
	export default {
		mounted() {
			
			this.showCategoryIndex = 0;
			uni.request({
				url:this.config.url+'category',
				data: {
					"token": "eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9..B32WfMWQ-0QJ1VtEbhxXgtT-nBqc8GwJb3ANBhy8BxU"

				},
				method: "post",
				success: (res) => {

					console.log(res);
					this.categoryList = res.data.data;
                    this.mingcheng = this.categoryList[0].title; 
					this.categoryChild=this.categoryList[0].children;
				}

			})
		},
		data() {
			return {
				showCategoryIndex: 0,
				headerPosition: "fixed",
				city: "北京",
				//分类列表
				categoryList: [],
				mingcheng: "",
				categoryChild:[],
				first:"",
				goodsTitle:""
			}
		},
		onPageScroll(e) {
			//兼容iOS端下拉时顶部漂移
			if (e.scrollTop >= 0) {
				this.headerPosition = "fixed";
			} else {
				this.headerPosition = "absolute";
			}
		},
		onLoad() {
			this.amapPlugin = new amap.AMapWX({
				//高德地图KEY，随时失效，请务必替换为自己的KEY，参考：http://ask.dcloud.net.cn/article/35070
				key: '5b9b64be2413fc19c26683fcf0de890f'
			});
			//定位地址
			this.amapPlugin.getRegeo({
				success: (data) => {
					this.city = data[0].regeocodeData.addressComponent.city.replace(/市/g, ''); //把"市"去掉
				}
			});
		},
		methods: {
			// 物品列表跳转
			goodsList(index){
			  var title=this.mingcheng
			   var index1=JSON.stringify(index)
		       var key1=JSON.stringify(this.first)
				uni.navigateTo({
					url:"/pages/goods/goods-list?id="+index1+"&key="+key1+"&title="+title
				})
			},
			//消息列表
			toMsg() {
				uni.navigateTo({
					url: '../msg/msg'
				})
			},
			//分类切换显示
			showCategory(index,key) {
				console.log(index,key)
				this.showCategoryIndex = index;
				this.first=key;
				this.mingcheng = this.categoryList[index].title;
				this.categoryChild=this.categoryList[index].children;
			},
		
			//搜索跳转
			toSearch() {
				uni.showToast({
					title: "建议跳转到新页面做搜索功能"
				});
			}
		},

	}
